.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

.loader-container {
  display: none;
}

.common-style {
  justify-content: flex-start;
  align-self: center;
  align-items: flex-start;
  display: flex;
}

.show-loader-container .loader-container {
  display: flex;
}

.ss-main {
  width: 20% !important;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.error {
  display: none;
}

.show-error {
  display: block;
}

.cat-card {
  display: flex;
}

.cat-image {
  max-width: 50%;
  height: auto;
}

.cat-details {
  box-sizing: border-box;
  background: radial-gradient(circle, #fff, #ffefba, #f2f2df, #f2e0ac);
  margin-left: 10px;
  padding: 30px;
}

